import React, { Component } from 'react';
import Card from './Card';
import CardGrid from './CardGrid';
import Section from './Section';

class Colors extends Component {
  render() {
    return (
      <Section title="Colors">
        <CardGrid>
          <Card
            cardImg="https://lh3.googleusercontent.com/-65BnAkhhZf0-X51t20gPoAFZ4cw-2w8vIFcSrR7G2RDiImuUerAPE0r2mjPQvHaG0bx=w300"
            cardURL="https://coolors.co/"
            cardName="Coolors"
            cardDesc="The super fast color schemes generator! Create, save and share perfect palettes in seconds!"
            imgHasBackground={true}
            cardCategory="Define your color palette"
          />
          <Card
            cardImg="http://themakers.io/tools/wp-content/uploads/2016/11/color_wheel_730-uai-258x258.png"
            cardURL="http://vidsbee.com/ColorPick/"
            cardName="ColorPick"
            cardDesc="An eye-dropper & color-picker tool that allows you to select color values from webpages."
            imgHasBackground={true}
            cardCategory="Find colors"
          />
          <Card
            cardImg="http://themakers.io/tools/wp-content/uploads/2016/11/logo-1.gif"
            cardURL="http://www.colorhunt.co/"
            cardName="Color Hunt"
            cardDesc="Curated collection of beautiful colors, updated daily."
            imgHasBackground={true}
            cardCategory="Define your color palette"
          />
        </CardGrid>
      </Section>
    );
  }
}

export default Colors;



